.gpt3_features{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gpt3_features_heading{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}

.gpt3_features_heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}

.gpt3_features_heading p{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    margin-top: 2rem;
}

.gpt3_features_container{
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px){
    .gpt3_features{
        flex-direction: column;
    }

    .gpt3_features_heading{
        margin: 0  0 2rem 0;
    }
}

@media screen and (max-width: 550px){
    .gpt3_features_heading h1{
        font-size: 28px;
        line-height: 38px;
    }

   
}