.gpt3_whatgpt3{
    display: flex;
    flex-direction: column;
    padding: 2rem;
    background: var(--color-footer);
}

.gpt3_whatgpt3_feature {
    display: flex;
}

.gpt3_whatgpt3_feature .gpt3_features_container_feature{
    margin: 0;
}

.gpt3_whatgpt3_feature .gpt3_features_container_feature_text{
    max-width: 700px;
}

.gpt3_whatgpt3_heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}

.gpt3_whatgpt3_heading h1{
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    max-width: 510px;
    font-family: var(--font-family);

}

.gpt3_whatgpt3_heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext)

}

.gpt3_whatgpt3_container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

.gpt3_whatgpt3_container .gpt3_features_container_feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.gpt3_whatgpt3_container .gpt3_features_container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3_whatgpt3_feature .gpt3_features_container_feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3_whatgpt3_feature .gpt3_features_container_feature_text {
        margin-top: 0.5rem;
    }

    .gpt3_whatgpt3_heading{
        flex-direction: column;
        align-items: flex-start;
    }

    .gpt3_whatgpt3_heading p{
        margin-top: 1rem;
    }

    .gpt3_whatgpt3_heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3_whatgpt3_container .gpt3_features-container_feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}