.gpt3_features_container_feature{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start ;
    flex-direction: row;
    margin: 1rem;
}

.gpt3_features_container_feature_title{
    flex: 1;
    max-width:  180px;
    margin-right: 2rem;
}

.gpt3_features_container_feature_title h1{
    font-family: var(--font-family);
    font-weight: 800;
    color: #FFFFFF;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
}

.gpt3_features_container_feature_title div{
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 0.25rem;
}

.gpt3_features_container_feature_text{
    max-width: 330px;
    flex: 2;
    display: flex;
   
}

.gpt3_features_container_feature_text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text) ;
}

@media screen and (max-width: 550px){
    .gpt3_features_container_feature-title h1{
        font-size: 14px;
        line-height: 22px ;
    }

    .gpt3_features_container_feature-text{
        max-width: 500px;
    }
    .gpt3_features_container_feature-text p{
        font-size: 12px;
        line-height: 20px;
       
    }

    .gpt3_features_container_feature{
        margin: 1rem 0;
    }
}

